import { mapState } from "vuex";
export default {
  name: "ArticleMeta",
  computed: {
    ...mapState(["user"]),
  },
  props: {
    article: {
      type: Object,
      require: true,
      default: {},
    },
  },
  head() {
    return {
      title: `${this.article.title} - RealWorld`,
      meta: [
        {
          hid: "description",
          name: "description",
          content: this.article.description,
        },
      ],
    };
  },
};
