import { getCommentsArticles } from "@/api/article";
export default {
  name: "ArticleComments",
  props: {
    article: {
      type: Object,
      require: true,
      default: {},
    },
  },
  data() {
    return {
      comments: [],
    };
  },
  async mounted() {
    const { data } = await getCommentsArticles(this.article.slug);
    this.comments = data.comments;
  },
};
